.swiper-pagination-bullet {
  &.swiper-pagination-bullet-active {
    background-color: var(--accent-color);
    &:first-child {
      background-image: url(../images/icons/home.svg);
      background-size: cover;
      border-radius: unset;
      background-color: transparent;
      width: 13px;
      height: 13px;
    }
  }
}

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;

  &:first-child {
    background-image: url(../images/icons/home-silver.svg);
    background-size: cover;
    border-radius: unset;
    background-color: transparent;
    width: 13px;
    height: 13px;
    opacity: 1;
  }
}

.swiper-pagination {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin: 0;
  grid-gap: 10px;
}

.swiper {
  width: 100%;
  height: 100%;
  margin-bottom: 32px;
}
