.footer {
    background-color: var(--light-pink-backgrount-color);
    padding-bottom: 20px;
    position: relative; 
    @media screen and (min-width: 768px) {
        padding-bottom: 8px;
    }
   
}

    .footer::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 78px;
        display: block;
        width: 100%;
        height: 1px;
        background: #E1E1E1;
        @media screen and (min-width: 768px) {
            bottom: 42px;
        }
    }



.footer-title{
    font-family: 'Titan One';
    font-weight: 400;
    font-size: 18px;
    line-height: 1.17;
    text-transform: uppercase;
    color: var(--lable-text-color);
    max-width: 232px;
    @media screen and (min-width: 768px) {
            font-size: 26px;
            line-height: 1.15;
            letter-spacing: 0.04em;
            max-width: 340px;
    }
}

.footer-title-two{
    font-family: 'Titan One';
    font-weight: 400;
    font-size: 34px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--accent-color);
    max-width: 200px;
    margin-top: 16px;
    margin-bottom: 40px;
    display: block;
    @media screen and (min-width: 768px) {
        font-size: 48px;
        line-height: 1.15;
        letter-spacing: 0.06em;
        min-width:340px;
        margin-top: 12px;
        margin-bottom: 0;
    }
}

.footer-contact-text{
    font-weight: 700;
    font-size: 12px;
    line-height: 1.83;
    text-align: right;
    color: var(--text-color);
    margin-bottom: 56px;
}

.footer-contact-tel{
    font-weight: 700;
    font-size: 12px;
    line-height: 1.83;
    text-align: right;
    color: var(--accent-color);
}

.text-bottom{
    font-weight: 700;
    font-size: 14px;
    line-height: 1.5;
    color: var(--secondary-text-color);
    text-align: center;
    display: block;
    margin: 0 auto;
    @media screen and (max-width: 767px) {
    max-width: 140px;
    }
    @media screen and (min-width: 768px) {
    line-height: 1.86;
    letter-spacing: 0.02em;
    }
}

.footer-link{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: #FEA5BB;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 250ms var(--timing-function);
    @media screen and (min-width: 768px) {
    width: 34px;
    height: 34px;
    }
}

.footer-icon {
    fill: var(--primary-text-color);
    width: 19px;
    height: 19px;
    @media screen and (min-width: 768px) {
    width: 20px;
    height: 20px;
}
}

.footer-link:hover,
.footer-link:focus{
    background-color: var(--accent-color);
}

.footer-list{
    display: flex;
    justify-content: end;
    margin-bottom: 40px;
}

.footer-item + .footer-item{
    margin-left: 28px;
}

.footer-wrap{
    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: space-between;
        margin-bottom: 48px;
    }
}